import React from 'react';
import SectionTitle from '../shared/section-titles';
import { CheckoutPackage } from './CheckoutPackage';
import { Step1 } from './Step1';
import { Step2 } from './Step2';
import { StepForm } from './StepForm';

export const StepWrapper = ({ title, dispatch, parentState }) => {
  const { tvCount, packageInfo, orderNumber, currentStep } = parentState;

  const steps = [
    <Step1
      dispatch={dispatch}
      tvCount={tvCount}
      isInternetOnly={packageInfo.isOnlyInternet}
    />,
    <Step2
      dispatch={dispatch}
      packageInfo={packageInfo}
    />,
    <StepForm
      dispatch={dispatch}
      parentState={parentState}
    />
  ];

  return (<>
    <div className='container px-0 py-4 w-100'>
      <SectionTitle>
        <h1>{title}</h1>
        <hr className='center mb-5' />
      </SectionTitle>

      <div className='row w-100 m-0 p-0'>
        <div className='col-8'>
          {steps[2]}
          {/* {steps[currentStep]} */}
        </div>
        <div className='col-4'>
          <h2 className='mb-5'>Избран пакет</h2>
          <CheckoutPackage
            packageInfo={packageInfo}
            orderNumber={orderNumber}
          />
        </div>

      </div>

    </div>
  </>);
};

export const numberChecker = (value) => {
  return /^\d*$/.test(value) &&
    (value === "" || parseInt(value) <= 20);
}
